import React, { useState } from 'react';
import './index.css';
import Todo from './Todo';

const App = () => {

    const[itemList, setItemList] = useState("");
    const[Item, setItem] = useState([]);

    const inputEvent = (event) => {
        setItemList(event.target.value);
    }

    const AddItem = () => {
        setItem((preValue) => {
            return[...preValue, itemList]
        })
        setItemList("")
    }

    const deleteItem = (id) => {
        setItem((preValue) => {
            return preValue.filter((array, index) => {
                return id!==index
            })
        })
    }

    return(
        <>
        <div className="main_div">
            <div className="todo">
                <h1>ToDo List</h1>
                <br/>
                <input type="text" placeholder="Add a item" onChange={inputEvent} value={itemList}/>
                <i className="fa fa-plus" aria-hidden="true" onClick={AddItem}></i>
                <ol>
                    {Item.map((itemVal,index) => {
                        return(
                            <Todo text={itemVal}
                                key={index}
                                id={index}
                                onSelect={deleteItem}
                            />
                        )
                    })}
                </ol>
            </div>
        </div>
        </>
    )
} 

export default App;