import React from 'react';

const Todo = (props) => {
    return(
        <>
        <div className="list">
        <li>{props.text}</li>
        <i className="fa fa-trash" aria-hidden="true" onClick={() => {
            props.onSelect(props.id)
        }}></i>
        </div>
        </>
    )
}

export default Todo;